	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2005/01/betandwin.gif\" border=\"0\" height=\"27\" width=\"77\" alt=\"betandwin.gif\" align=\"right\" />Maybe it's just me, but isn't it a bit funny (as in strange) that most of the German online stories about the current <a href=\"http://news.google.com/news?hl=en&ned=us&q=Hoyzer&btnG=Search+News\">Bundesliga match fixing scandal</a> feature a banner ad for online sports betting (<a href=\"http://www.bild.t-online.de/BTO/sport/aktuell/2005/01/29/schiri__beobachtung/schiri__heute__alles__rechtens.html\">Bild</a>, <a href=\"http://www.spiegel.de/sport/fussball/0,1518,339014,00.html\">SpOn</a>, <a href=\"http://www.kicker.de/content/news/artikel.asp?object=310133&folder=3100\">Kicker</a>, <a href=\"http://www.sport1.de/coremedia/generator/www.sport1.de/Sportarten/Fussball/Main.html\">Sport1</a>)?</p>\n<p>It's also funny that the referee Hoyzer hat a thing about Hamburg; 3 of the 5 supposedly fixed matches went against either HSV or FC St. Pauli. I'm kind of waiting for the revelation that in the cup match against Paderborn, Hoyzer had actually bet on HSV, but they still lost.</p>","fields":{"title":"Bet & Win","date":"29 Jan 2005","url":"https://papascott.de/2005-01-29-bet-win/"}}},"pathContext":{"slug":"/2005-01-29-bet-win/","prev":{"fields":{"title":"Use Photoshop","date":"28 Jan 2005","slug":"/2005-01-28-use-photoshop/"}},"next":{"fields":{"title":"I Fear No Evil","date":"30 Jan 2005","slug":"/2005-01-30-i-fear-no-evil/"}}}}

